.about {
  &__title {
    margin-bottom: 32px;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: $colorWhite;

    @include onDesktop {
      margin-bottom: 0;
      font-size: 64px;
      line-height: 100%;
    }
  }

  &__paragraph {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
    color: $colorWhite;
  }
}
